import React, { Component } from 'react'
import {QuizData} from './QuizData'
 class Quiz extends Component {
     state={
         userAnswer:null,
         currentQuestion:0,
         options:[], 
         quizEnd :false,
         score:0,
         disabled:true
     }
     loadQuiz=()=>{
         const {currentQuestion}=this.state;
         this.setState(()=>{
             return {
                 questions:QuizData[currentQuestion].question,
                 options:QuizData[currentQuestion].option,
                 answers:QuizData[currentQuestion].answer
             }
         })
     }
     componentDidMount(){
         this.loadQuiz();
     }
     nextQuestionHandler=()=>{
         const {userAnswer,answers,score}=this.state;
         this.setState({
             currentQuestion:this.state.currentQuestion+1
         })
         console.log(this.state.currentQuestion)
         if(userAnswer===answers){
            this.setState({
                score :score+1
            })
         }
     }
     componentDidUpdate(prevProps,prevState){
         const {currentQuestion}=this.state;
if(this.state.currentQuestion!==prevState.currentQuestion){
    this.setState(()=>{
        return{
            disabled:true,
            questions:QuizData[currentQuestion].question,
            options:QuizData[currentQuestion].option,
            answers:QuizData[currentQuestion].answer
        };
    })
}
}
     checkAnswer=answer=>{
         this.setState({
             userAnswer:answer,
             disabled:false
         })
     }
     finishHandler =()=>{
         if(this.state.currentQuestion === QuizData.length-1)
         {
             this.setState({
                 quizEnd :true
             })
         }
     }
    render() {
        const {questions,options,currentQuestion,quizEnd}=this.state;
        if(quizEnd){
            return(
                <div>
                    <h2>Quiz Over </h2>
                    <h1>Congratulations!</h1>
                    <p>Final Score :<h2>{this.state.score}</h2> points</p>
                    <h4>The Correct Answers for the questions are :</h4>
                    <ul>
                        {QuizData.map((item,index)=>(
                            <li className="ui floating message options"
                            key={index}>
                            {item.answer}</li>
                        ))}
                        </ul>
                    </div>
            )
        }
        return (
            <div className="App">
         <h3> {questions}</h3>
         <span>Questions {currentQuestion} out of {QuizData.length-1} </span>         
          {options.map(option=>(
          <p key={option.id} className={'ui floating message options'}
          onClick={() => this.checkAnswer(option)}
          >
              {option}
            </p>
          ))}
          {currentQuestion < QuizData.length-1 && 
          <button
          className="ui inverted button" disabled={this.state.disabled} onClick={this.nextQuestionHandler}>Next</button>
          }
          {currentQuestion === QuizData.length-1 && 
            <button className="ui inverted button" disabled={this.state.disabled} onClick={this.finishHandler}>Finish</button>
          }
          </div>
        );
    }
}

export default Quiz